/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background: #000000;
  color: #FFFFFF;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Mulish", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.container {
  margin: 2rem 3rem;
}

h1, h2, h3 {
  font-family: "Cairo", sans-serif;
  margin: 0 0 2.5rem 0;
  color: #FFFFFF;
  text-align: center;
}

h1 {
  font-size: 5rem;
}

h3 {
  font-size: 4.8rem;
  text-align: center;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.head-contac {
  background-color: #1a1b15ea;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  margin: 0;
  z-index: 100;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
@media (min-width: 480px) {
  .head-contac {
    flex-direction: row;
  }
}
@media (min-width: 768px) {
  .head-contac span {
    font-size: 2rem;
  }
}
.head-contac svg {
  margin: 0 1rem 0 2rem;
}
.head-video {
  display: block;
  width: 100%;
}
.head-contenedor-video {
  position: relative;
  max-height: 50rem;
  overflow: hidden;
}
.head-overlay {
  position: absolute;
  width: 100%;
  height: 93%;
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#891c1e+0,9e37f2+67,ffffff+100&0.8+0,0.5+100 */
  background: #0e247a;
  background: linear-gradient(90deg, rgba(233, 233, 233, 0.213) 0%, rgba(131, 194, 203, 0.3449754902) 26%, rgba(138, 166, 250, 0.192) 52%, rgba(63, 204, 255, 0.226) 87%, rgba(209, 239, 245, 0.205) 100%);
  background: linear-gradient(135deg, rgba(200, 200, 201, 0.24) 0%, rgba(118, 175, 228, 0.178) 67%, rgba(255, 255, 255, 0.5) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#cc891c1e", endColorstr="#80ffffff",GradientType=1 );
  /* IE6-9 fallback on horizontal gradient */
  overflow: hidden;
}
@media (min-width: 480px) {
  .head-overlay {
    width: 100%;
    height: 93%;
  }
}
@media (min-width: 768px) {
  .head-overlay {
    width: 100%;
    height: 97%;
  }
}
@media (min-width: 1024px) {
  .head-overlay {
    width: 100%;
    height: 100%;
  }
}
.head-contenido-video {
  display: flex;
  align-items: center;
  height: 100%;
  position: relative;
  z-index: 2;
}
.head-writer {
  background-color: #bce2f52a;
  font-size: 2rem;
  padding: 1rem 2rem;
  font-weight: 400;
  margin: 7rem 0 0 0;
}
@media (min-width: 768px) {
  .head-writer {
    font-size: 5rem;
    margin: 15rem 0;
  }
}
.head-writer span {
  color: #FFFFFF;
  font-size: 4rem;
  margin: 1rem 0 0 0;
  font-weight: 850;
}
@media (min-width: 480px) {
  .head-writer span {
    font-size: 6rem;
  }
}
@media (min-width: 768px) {
  .head-writer span {
    font-size: 10rem;
  }
}

.contenedor-menu {
  background: #28a8b8;
  background: linear-gradient(144deg, rgba(40, 168, 184, 0.1713060224) 1%, rgba(244, 250, 251, 0.1853116246) 26%, rgba(231, 235, 247, 0.1264880952) 56%, rgba(137, 200, 223, 0.1965161064) 87%, rgba(177, 225, 235, 0.2693452381) 100%);
  display: -ms-flex;
  display: -webkit-flex;
  display: flex;
  justify-content: space-between;
  position: -webkit-sticky;
  position: sticky;
  top: 20px;
}
.contenedor-menu .logo-menu {
  width: 30%;
  width: 8rem;
  margin: 2rem 5rem 1rem 3rem;
  background-color: #00000013;
  color: #FFFFFF;
}
.contenedor-menu .logo-menu:hover {
  cursor: pointer;
}
.contenedor-menu nav {
  margin: 5rem 0 0 0;
  width: 70%;
  text-align: right;
}
@media (min-width: 768px) {
  .contenedor-menu nav {
    margin: 3rem 0 0 0;
  }
}
.contenedor-menu nav a {
  font-family: "Mulish", sans-serif;
  font-weight: 700;
  color: #FFFFFF;
  background: rgba(255, 255, 255, 0.062);
  /* color de fondo */
  color: #ffffff;
  /* color de fuente */
  border: 2px solid #4641d700;
  /* tamaño y color de borde */
  border-radius: 10px;
  padding: 16px 20px;
  border-radius: 3px;
  /* redondear bordes */
  position: relative;
  z-index: 1;
  overflow: hidden;
  display: none;
}
.contenedor-menu nav a:hover {
  color: #f8f8f8c0;
  background-color: rgba(92, 91, 91, 0.158);
}
.contenedor-menu nav a:after {
  content: "";
  background: #efeff021;
  /* color de fondo hover */
  position: absolute;
  z-index: -1;
  padding: 10px;
  display: none;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  transform: scale(0, 0) rotate(-180deg);
  transition: all 0.3s ease;
}
@media (min-width: 768px) {
  .contenedor-menu nav a:after {
    display: block;
  }
}
.contenedor-menu nav a:hover::after {
  transition: all 0.3s ease-out;
  transform: scale(1, 1) rotate(0deg);
}
@media (min-width: 480px) {
  .contenedor-menu nav a {
    display: none;
    margin: 0 0.1rem;
    font-size: 0.8rem;
  }
}
@media (min-width: 768px) {
  .contenedor-menu nav a {
    display: inline-block;
    margin: 0.8rem 0.2rem;
    font-size: 1.2rem;
  }
}
@media (min-width: 1024px) {
  .contenedor-menu nav a {
    display: inline-block;
    margin: 1.2rem 0.3rem;
    font-size: 1.6rem;
  }
}

.services {
  text-align: center;
}
.services-title {
  width: 40%;
  background-color: rgba(185, 185, 185, 0);
  text-align: center;
  margin: 1rem auto;
}
@media (min-width: 768px) {
  .services-title {
    font-size: 5rem;
    margin: 0 auto;
  }
}
@media (min-width: 1024px) {
  .services-title {
    font-size: 5.8rem;
    margin: 0 auto;
  }
}
.services-type {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  margin: 0;
}
@media (min-width: 480px) {
  .services-type {
    flex-direction: column;
  }
}
@media (min-width: 768px) {
  .services-type {
    flex-direction: column;
  }
}
@media (min-width: 1024px) {
  .services-type {
    flex-direction: row;
    margin: 3rem 4rem;
  }
}
.services-web-informativa, .services-web-tinendaVirtual, .services-web-aulaVirtual {
  margin: 2rem auto;
  width: 80%;
  transition: 0.3s;
}
@media (min-width: 768px) {
  .services-web-informativa, .services-web-tinendaVirtual, .services-web-aulaVirtual {
    width: 70%;
  }
}
@media (min-width: 1024px) {
  .services-web-informativa, .services-web-tinendaVirtual, .services-web-aulaVirtual {
    width: 27%;
  }
}
.services-web-informativa:hover, .services-web-tinendaVirtual:hover, .services-web-aulaVirtual:hover {
  cursor: pointer;
}
@media (min-width: 1024px) {
  .services-web-informativa:hover, .services-web-tinendaVirtual:hover, .services-web-aulaVirtual:hover {
    margin-top: -10px;
  }
}
.services-web-informativa h3, .services-web-tinendaVirtual h3, .services-web-aulaVirtual h3 {
  padding: 2.6rem;
  background: #0f0886;
  background: linear-gradient(180deg, rgba(3, 242, 255, 0.614) 0%, rgba(9, 108, 121, 0.623) 35%, rgba(0, 212, 255, 0.3897934174) 100%);
  border-radius: 20px 20px 0 0;
  margin: 0;
  font-size: 1.9rem;
}
@media (min-width: 480px) {
  .services-web-informativa h3, .services-web-tinendaVirtual h3, .services-web-aulaVirtual h3 {
    font-size: 3.3rem;
  }
}
@media (min-width: 768px) {
  .services-web-informativa h3, .services-web-tinendaVirtual h3, .services-web-aulaVirtual h3 {
    font-size: 4.1rem;
  }
}
.services-web-informativa ul, .services-web-tinendaVirtual ul, .services-web-aulaVirtual ul {
  background-color: #ffffffb2;
  margin: 0;
  padding: 2rem 1.5rem;
  border-radius: 0 0 20px 20px;
}
.services-web-informativa li, .services-web-tinendaVirtual li, .services-web-aulaVirtual li {
  text-decoration: none;
  list-style: none;
  text-align: left;
  padding: 0.5rem 0;
  font-size: 1.6rem;
}

@keyframes moveUp {
  from {
    top: 0px;
  }
  to {
    top: -10px;
  }
}
.we-offer-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .we-offer-container {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}
@media (min-width: 1024px) {
  .we-offer-container {
    flex-direction: row;
  }
}
.we-offer-img {
  width: 80%;
  margin: 2rem 2rem 1rem 1.5rem;
  text-align: center;
  filter: drop-shadow(5%);
}
.we-offer-img-img {
  width: 100%;
  padding: 20px;
  border-radius: 38px;
}
.we-offer-text {
  width: 80%;
}

.footer {
  display: flex;
  flex-direction: row;
  background-color: #363636;
}

.container-imagen-movil {
  position: relative;
  width: 100%;
}
.container-imagen-movil .nevado {
  position: absolute;
  width: 100%;
  z-index: 2;
}

#subscribe {
  background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url(../img/MINI.jpg) fixed center center;
  background-size: cover;
  padding: 40px 0;
  position: absolute;
  width: 100%;
  height: 270px;
  z-index: 1;
}
@media (min-width: 480px) {
  #subscribe {
    padding: 9px 0rem;
  }
}
#subscribe .subscribe-title {
  color: #fff;
  font-size: 28px;
  font-weight: 700;
}
#subscribe .subscribe-text {
  color: #fff;
}
#subscribe .subscribe-btn-container {
  text-align: center;
  padding-top: 20px;
}

.subscribe-btn {
  font-family: "Roboto Slab", serif;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 8px 20px;
  border-radius: 2px;
  transition: 0.5s;
  margin: 10px;
  border: 2px solid #fff;
  color: #fff;
  position: absolute;
  z-index: 3;
  top: 145px;
  left: 50px;
}
.subscribe-btn:hover {
  background: #afb1a53a;
  border: 2px solid #afb1a53a;
  cursor: pointer;
}

#blog {
  margin-top: 30rem;
}
#blog .blog-container {
  display: flex;
  justify-content: center;
  gap: 3.2rem;
  margin: 4rem 0;
}
#blog .blog {
  background-color: black;
  margin: 0 1rem;
}
#blog .blog-imagen {
  width: 80%;
  text-align: center;
}

#portafolio {
  margin-top: 10rem;
}
#portafolio .paginas {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: auto;
  margin: 4rem 0;
}
#portafolio .pagina {
  margin: 3.5rem;
}
#portafolio .pagina-imagen {
  width: 80%;
}

#social .container-sociales {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: auto;
}
#social .redsocioal-imagen {
  background-color: azure;
}